<template>
    <div class="event-card mdl-card mdl-shadow--4dp" v-bind:style="{ background: 'url(' + event.coverUrl + ')' }">
        <div class="mdl-card__title mdl-card--expand">
            <h3><a v-bind:href="event.url" target="_blank">{{event.name}}</a></h3>
        </div>
        <div class="mdl-card__actions mdl-card--border">
            <span>{{eventDate}}</span>
        </div>
    </div>

</template>
<script>
    export default {
        props: ['event'],
        computed: {
            eventDate: function () {
                return this.getEventTime();
            }
        },
        methods: {
            getEventTime: function () {
                let result = '';
                let startTime = new Date(this.event.start_time);
                let endTime = new Date(this.event.end_time);

                if (startTime.toDateString() === endTime.toDateString())
                    return startTime.toLocaleDateString() + ' ' + startTime.toLocaleTimeString() + ' - ' + endTime.toLocaleTimeString();

                return startTime.toLocaleDateString() + ' - ' + endTime.toLocaleDateString();
            }
        }
    }

</script>
<style>
    .event-card.mdl-card {
        margin: 5px 10px;
        background-size: 100%!important;
        max-width: 100%;
    }
    
    .event-card>.mdl-card__title {
        align-items: flex-start;
        background: rgba(0, 0, 0, 0.2)!important;
    }
    
    .event-card>.mdl-card__title a {
        color: white;
        font-size: 0.6em;
        text-decoration: none;
        font-weight: 400;
    }
    
    .event-card > .mdl-card__actions {
        background: rgba(0, 0, 0, 0.5);
        color: #fff;
        font-size: 0.9em;
    }
</style>


